.MainMenuLinks {
    font-size: 18px;
    margin: 2px 10px;
    color: grey;
    text-decoration: none;
}

.MainMenuLinks:hover {
    color: white;
}

.OptionsBackground {
    background-color: black;
    width: 100%;

}

.Options {
    margin-left: 40%;
    padding: 15px;
}

.MainMenuLinks:focus {
    outline: none !important;
    border-color: #71a9ce;
