body {
    font-family: 'Arimo', sans-serif;
}

.tabUl li input,
.tabUl li textarea,
.tabUl li button {
    padding: 10px;
    margin-top: 15px;
    width: 90%;
    font-size: 20px;
    margin-bottom: 10px;
}

h2 {
    color: #000;
    text-align: center;
    font-size: 2em;
}

.warpper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;

}

.tab {

    cursor: pointer;
    padding: 10px 20px;
    margin: 0px 2px;
    background: #000;
    display: inline-block;
    color: #fff;
    border-radius: 3px 3px 0px 0px;
    box-shadow: 0 0.5rem 0.8rem #00000080;
}

.panels {
    background: #fffffff6;
    box-shadow: 0 2rem 2rem #00000080;
    min-height: 200px;
    width: 100%;
    max-width: 1500px;
    border-radius: 3px;
    overflow: hidden;
    padding: 20px;
}

.panel {
    display: none;
    -webkit-animation: fadein .8s;
            animation: fadein .8s;
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.panel-title {
    font-size: 1.5em;
    font-weight: bold
}

.radio {
    display: none;
}

#one:checked~.panels #one-panel,
#two:checked~.panels #two-panel {
    display: block
}

#one:checked~.tabs #one-tab,
#two:checked~.tabs #two-tab {
    background: #fffffff6;
    color: #000;
    border-top: 3px solid #000;
}
.columns li input,
.rows li input,
.rows li button {
    padding: 10px;
    margin-top: 15px;
    margin-bottom: 15px;
    font-size: 20px;
    width: 90%;
}

.columns li {
    display: inline;
}

.rows li {
    display: block;
}
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}
